<template>
  <div class="order-wrap">
    <div class="order-list-choose">
      <div class="order-list-option">
        
        <v-selection :selections="products" @on-change="productChange"></v-selection>
      </div>
      <div class="order-list-option">
        
        <v-date-picker @change="changeStartDate"></v-date-picker>
      </div>
      <div class="order-list-option">
        
        <v-date-picker @change="changeEndDate"></v-date-picker>
      </div>
      <div class="order-list-option">
        
        <input type="text" v-model="query" class="order-query">
      </div>
    </div>
    <div class="order-list-table">
      <table>
        <tr>
          <th v-for="head in tableHeads" @click="changeOrderType(head)" :class="{active:head.active}">{{ head.label }}</th>
        </tr>
        <tr v-for="item in tableData" :key="item.period">
          <td v-for="head in tableHeads">{{ item[head.key] }}</td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script >
  import VSelection from '../components/selection'
  import VDatePicker from '../components/datepicker'
  export default {
    components: {
      VSelection,
      VDatePicker
    },
    data () {
      return {
        query: '',
        productId: 0,
        startDate: '',
        endDate: '',
        products: [
          {
            label: '',
            value: 0
          },
          {
            label: '',
            value: 1
          },
          {
            label: '',
            value: 2
          },
          {
            label: '',
            value: 3
          }
        ],
      tableHeads: [
        {
          label: '',
          key: 'orderId'
        },
        {
          label: '',
          key: 'product'
        },
        {
          label: '',
          key: 'version'
        },
        {
          label: '',
          key: 'period'
        },
        {
          label: '',
          key: 'date'
        },
        {
          label: '',
          key: 'buyNum'
        },
        {
          label: '',
          key: 'amount'
        }
      ],
      currentOrder: 'asc',
      tableData: []
      }
    },
    methods: {
      
      productChange (obj) {
        this.productId = obj.value
        this.getTableData();
      },
      changeStartDate(date) {
        this.startDate = date
        this.getTableData();
      },
      changeEndDate(date) {
        this.endDate = date
        this.getTableData();
      },
      getTableData () {
        let reqParam = {
          query: this.query,
          productId: this.productId,
          startDate: this.startDate,
          endDate: this.endDate
        }
        this.$http.post('./api/getOrderList', reqParam)
        .then((res) => {
          this.tableData = res.data.list
        },(err) => {

        })
      },
      changeOrderType (headItem) {
        this.tableHeads.map((item) => {
          item.active = false
          return item
      })
        headItem.active = true
        if (this.currentOrder === 'asc') {
          this.currentOrder = 'desc'
        }
        else if (this.currentOrder === 'desc') {
          this.currentOrder = 'asc'
        }
          this.tableData = _.orderBy(this.tableData, headItem.key,   this.currentOrder)
      }
    },
    mounted() {
      this.getTableData();
    },
    watch: {
      query() {
        this.getTableData();
      }
    }
  };
</script>

<style >
  .order-wrap {
    width: 1200px;
    margin: 0 auto;
    overflow: hidden;
    min-height: 800px;
  }
  .order-list-choose {
    margin-top: 15px;
  }
  .order-list-option{
    display: inline-block;
    padding-left: 15px;
  }
  .order-list-option:first-child{
    padding-left: 0;
  }
  .order-query {
    height: 25px;
    line-height: 25px;
    border: 1px solid #e3e3e3;
    outline: none;
    text-indent: 10px;
  }
  .order-list-table{
    margin-top: 20px;
  }
  .order-list-table table{
    width: 100%;
    background: #fff;
  }
  .order-list-table td,
  .order-list-table th {
    border: 1px solid #e3e3e3;
    text-align: center;
    padding: 10px 0;
  }
  .order-list-table th {
    background: #4fc08d;
    color: #fff;
    border: 1px solid #4fc08d;
    cursor: pointer;
  }
  .order-list-table th.active {
    background: #35495e;
  }
</style>



